var bar = function (id, d3, width, height, barColor) {
      d3.select('.circle-bar').remove()
      // set the dimensions and margins of the graph
      var margin = {top: 20, right: 20, bottom: 30, left: 40},
          width = width - margin.left - margin.right,
          height = height - margin.top - margin.bottom;

      // set the ranges
      var x = d3.scaleBand()
                .range([0, width * 4])
                .padding(0.1);
      var y = d3.scaleLinear()
                .range([height, 0]);

      // append the svg object to the body of the page
      // append a 'group' element to 'svg'
      // moves the 'group' element to the top left margin
      var svg = d3.select(id).append("svg")
          .attr("width", (width + margin.left + margin.right) * 4)
          .attr("height", height + margin.top + margin.bottom)
          .attr("class", "circle-bar")
        .append("g")
          .attr("transform",
                "translate(" + margin.left + "," + margin.top + ")");

      // get the data
      // d3.csv("sales.csv", function(error, data) {
      //   if (error) throw error;

        // format the data
        // data.forEach(function(d) {
        //   d.sales = +d.sales;
        // });
        // console.log(data)
        // console.log(JSON.stringify(data))
        // Scale the range of the data in the domains
        var data = [
          {"salesperson":"#LiveFromVegas","sales":0.9},
          {"salesperson":"#UFC219","sales":0.6},
          {"salesperson":"Holm","sales":0.5},
          {"salesperson":"#RIZINFF","sales":0.5},
          {"salesperson":"Khabib","sales":0.46},
          {"salesperson":"Cris Cyborg","sales":0.43},
          {"salesperson":"Happy New Years Eve","sales":0.28},
          {"salesperson":"#selfiesfor5sos","sales":0.27},
          {"salesperson":"Chris","sales":0.26},
          {"salesperson":"Lisa","sales":0.2},
          {"salesperson":"Tom","sales":0.19},
          {"salesperson":"Stacy","sales":0.1},
          {"salesperson":"Charles","sales":0.09},
          {"salesperson":"Mary","sales":0.05}
        ]
        x.domain(data.map(function(d) {
            var string =  d.salesperson;
            if (string.length > 17){
              return  string.substring(0, 12) + "..."
            }else {
              return  string
            }
         }));
        y.domain([0, d3.max(data, function(d) { return d.sales; })]);

        // append the rectangles for the bar chart
        svg.selectAll(".bar")
            .data(data)
          .enter().append("rect")
          .transition()
          .duration(500)
            .attr("class", "bar")
            .attr("x", function(d) {
                var string =  d.salesperson;
                if (string.length > 17){
                  return  x(string.substring(0, 12) + "...")
                }else {
                  return  x(string)
                }
             })
            .attr("width", x.bandwidth())
            .attr("y", function(d) { return y(d.sales); })
            .attr("height", function(d) { return height - y(d.sales); })
            .attr("fill", barColor)

        // add the x Axis
        svg.append("g")
            .attr("transform", "translate(0," + height + ")")
            .call(d3.axisBottom(x));

        // add the y Axis
        svg.append("g")
            .attr("position", "fixed")
            .call(d3.axisLeft(y))

      // });





}
export default bar



