<script>
import ICountUp from 'vue-countup-v2'
import subTitle from '../../components/subtitle/index'
import Jquery from 'jquery'
var jQuery = window.$ = window.jquery = window.jQuery = Jquery
var Highcharts = require('highcharts/highstock')
require("moment/min/moment.min.js")
require("bootstrap/dist/css/bootstrap.css")
require("daterangepicker/daterangepicker.js")
require("daterangepicker/daterangepicker.css")
require("paginationjs/dist/pagination.js")
require("paginationjs/dist/pagination.css")
import * as d3 from 'd3'
import d3WordCloud from 'd3.layout.cloud'
import donut from '../../jsscript/donut'
import compareStock from '../../jsscript/compareStock'
import rgbatorgb from '../../jsscript/rgbatorgb'
import dateSelection from '../../jsscript/dateSelection'
import comparisionbar from '../../jsscript/comparisionbar'
import bar from '../../jsscript/bar'

//
export default {
  name: 'Index',
  components: {
      ICountUp,
      subTitle
    },
    data() {
      return {
        options: {
          useEasing: true,
          useGrouping: true,
          separator: ',',
          decimal: '.',
          prefix: '',
          suffix: ''
        },
        active_flag: 1,
        imgName: "trend.png"
      }
    },
    methods: {
      changeBg: function (num) {
        this.active_flag = num
      },
      callback: function(ins) {
        ins.update(ins.endVal + 5)
      },
      cubeAddCss: function (ele) {
        ele.parentElement.style.border = "4px solid #777"
      },
      cubeRemoveCss: function (ele) {
        ele.parentElement.style.border = "0"
      },
      cubeClick: function (ele) {
        var array = document.getElementsByClassName('cube-opa')
        for (var i = 0; i < array.length; i++) {
          array[i].style.opacity = 0.5
        }
        ele.parentElement.style.opacity = 1
        comparisionbar('#comparisionbar', d3)
      }
   },
   mounted () {

     var a_collection = document.getElementsByClassName('mfd-link')
     for (var i = 0; i < a_collection.length; i++) {
       var random_value = Math.random()
       a_collection[i].style.backgroundColor = rgbatorgb(30,126,190, random_value, 255, 255, 255)
       if (random_value < 0.3) {
         a_collection[i].style.color = "rgb(88, 88, 95)"
       }
     }
     // highstock
     compareStock(Highcharts, Jquery)
     //calendar
     // dateSelection('.second-left', Jquery, Pickmeup)
     //donut
     var data = [10, 8, 3, 4, 9]
     var name = ['Care', 'Fairness', 'Loyalty', 'Authority', 'Sanctity']
     donut('#secondmiddle', d3, data, name, 600, 600, bar)

    //bar
    bar("#secondmiddlebar", d3, 330, 330, "#62a0f8")

     //comparisionbar
     comparisionbar('#comparisionbar', d3)


     // daterangepicker
     $(function() {
       $('input[name="daterange"]').daterangepicker()
      })

    // page
    console.log(jQuery)
      jQuery('#page').pagination({
      dataSource: [1, 2, 3, 4, 5, 6, 7, 195,5, 6, 7, 195,5, 6, 7, 195,5, 6, 7, 195,195,5, 6, 7, 195,195,5, 6, 7, 195,195,5, 6, 7, 195],
      pageSize: 1,
      showPageNumbers: false,
      showNavigator: true,
      callback: function(data, pagination) {
          // template method of yourself
          console.log(1)
          // var html = template(data);
          // $('#data-container').html(html);
      }
      })
  }
}
